.mainForm {
    width: 500px;
 margin-left: 30%;
 padding: 10px;
 border: 1px solid black;
}
.inputContainer {
 display: flex;
 flex-direction: column;
 justify-content: center;
}
.labelName {
 display: inline-block;
 margin-bottom: 20px;
 font-size: 25px;
 font-weight: 500;
}
.inputName {
 display: block;
 max-width: 200px;
 height: 50px;
 margin-bottom: 30px;
 padding-left: 10px;
 border-radius: 5px;
 border: 1px solid rgba(33, 33, 33, 0.2);
}
.inputName:focus {
 outline: 4px solid #2196f3;
 cursor: pointer;
}
.buttonAddContact:hover{
    border-radius: 5px;
cursor: pointer;
background-color: rgb(79, 79, 243);
}
